import React, { Component } from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import size from 'lodash/size'
import ReadNext from '../ReadNext'

export default class SitePost extends Component {

  description(body) {
    let test = body.replace(/<blockquote>/g, '<blockquote class="blockquote">')
    if (test.match('<!--more-->')) {
      test = test.split('<!--more-->')
      if (typeof test[0] !== 'undefined') {
        return test[0]
      }
    }
    return test
  }

  startEnd(start, end) {
    if (!start && !end) return

    console.log(start, end);
    return start
  }

  categories(data) {
    if (!data) return

    const categories = []
    data.forEach((category, i) => {
      categories.push(
        <span className="badge badge-primary text-white" key={i}>
          {category}
        </span>
      )
    })
    return categories
  }

  more(isMore, path) {
    if (!isMore) return

    return (
      <Link to={path} className="readmore">
        <span className="btn btn-outline-primary btn-block"></span>
      </Link>
    )
  }

  render() {
    const { site, data, isIndex } = this.props

    const title = get(data, 'frontmatter.title')
    const path = get(data, 'frontmatter.path')
    const date = get(data, 'frontmatter.date')
    const startEnd = this.startEnd(get(data, 'frontmatter.start'), get(data, 'frontmatter.end'))
    const description = get(data, 'frontmatter.description')
    const html = get(data, 'html')
    const cate = get(data, 'frontmatter.category') || get(data, 'frontmatter.categories')
    const categories = cate ? this.categories(cate) : ''
    const footer = isIndex ? '' : <ReadNext data={site} />
    const desc = isIndex ? description || this.description(html) : html
    const more = isIndex ? this.more(!!desc, path) : ''

    return (
      <div className="container">
        <div className="articles col-md-12">
          <div className="article-wrap" key={path}>
            <div className="page-header">
              <Link to={path} style={{ boxShadow: 'none' }}>
                <h1>{title}</h1>
                {startEnd}
              </Link>
              {categories}
            </div>
            <div
              className="page-content"
              dangerouslySetInnerHTML={{ __html: desc }}
            />
            {more}
            {isIndex}
          </div>
        </div>
      </div>
    )
  }
}



